<template>
	<div>
		<el-dialog modal-class="edit_form_modal" class="edit_form" v-model="formVisible" :title="formTitle" width="60%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" :rules="rules">
				<el-row>
					<el-col :span="12">
						<el-form-item label="收入名称" prop="shourumingcheng">
							<el-input class="list_inp" v-model="form.shourumingcheng" placeholder="收入名称"
								 type="text" 								:readonly="!isAdd||disabledForm.shourumingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="收入金额" prop="shourujine">
							<el-input class="list_inp" v-model.number="form.shourujine" placeholder="收入金额"
								 type="number" 								:readonly="!isAdd||disabledForm.shourujine?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="收入备注" prop="shourubeizhu">
							<el-input class="list_inp" v-model="form.shourubeizhu" placeholder="收入备注"
								 type="text" 								:readonly="!isAdd||disabledForm.shourubeizhu?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="支出名称" prop="zhichumingcheng">
							<el-input class="list_inp" v-model="form.zhichumingcheng" placeholder="支出名称"
								 type="text" 								:readonly="!isAdd||disabledForm.zhichumingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="支出金额" prop="zhichujine">
							<el-input class="list_inp" v-model.number="form.zhichujine" placeholder="支出金额"
								 type="number" 								:readonly="!isAdd||disabledForm.zhichujine?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="支出说明" prop="zhichushuoming">
							<el-input class="list_inp" v-model="form.zhichushuoming" placeholder="支出说明"
								 type="text" 								:readonly="!isAdd||disabledForm.zhichushuoming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="收支结余" prop="shouzhijieyu">
							<el-input class="list_inp" v-model="shouzhijieyu" :readonly="true" placeholder="收支结余" />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="登记日期" prop="dengjiriqi">
							<el-date-picker
								class="list_date"
								v-model="form.dengjiriqi"
								format="YYYY 年 MM 月 DD 日"
								value-format="YYYY-MM-DD"
								type="datetime"
								:readonly="!isAdd||disabledForm.dengjiriqi?true:false"
								placeholder="请选择登记日期" />
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="登记内容" prop="dengjineirong">
							<el-input v-model="form.dengjineirong" placeholder="登记内容" type="textarea"
							:readonly="!isAdd||disabledForm.dengjineirong?true:false"
							/>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="cancel_btn" @click="closeClick">取消</el-button>
					<el-button class="confirm_btn" type="primary" @click="save"
						>
						提交
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
    import {
        useStore
    } from 'vuex';
    const store = useStore()
    const user = computed(()=>store.getters['user/session'])
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//基础信息
	const tableName = 'caiwuxinxi'
	const formName = '财务信息'
	//基础信息
	//form表单
	const form = ref({})
	const disabledForm = ref({
		shourumingcheng : false,
		shourujine : false,
		shourubeizhu : false,
		zhichumingcheng : false,
		zhichujine : false,
		zhichushuoming : false,
		shouzhijieyu : false,
		dengjiriqi : false,
		dengjineirong : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		shourumingcheng: [
		],
		shourujine: [
			{required: true,message: '请输入',trigger: 'blur'}, 
			{ validator: validateNumber, trigger: 'blur' },
		],
		shourubeizhu: [
		],
		zhichumingcheng: [
		],
		zhichujine: [
			{required: true,message: '请输入',trigger: 'blur'}, 
			{ validator: validateNumber, trigger: 'blur' },
		],
		zhichushuoming: [
		],
		shouzhijieyu: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		dengjiriqi: [
		],
		dengjineirong: [
		],
	})
	//表单验证
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const shouzhijieyu =computed(()=>{
		let c = form.value
		let a = c.shourujine-c.zhichujine
        form.value.shouzhijieyu = Number(a)?Number(parseFloat(a).toFixed(2)) : 0
        return Number(a)?Number(parseFloat(a).toFixed(2)) : 0
	})
	//methods

	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//重置
	const resetForm = () => {
		form.value = {
			shourumingcheng: '',
			shourujine: '',
			shourubeizhu: '',
			zhichumingcheng: '',
			zhichujine: '',
			zhichushuoming: '',
			shouzhijieyu: '',
			dengjiriqi: '',
			dengjineirong: '',
		}
	}
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
			form.value.dengjiriqi = context?.$toolUtil.getCurDate()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '新增' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '查看' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '修改' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='shourumingcheng'){
					form.value.shourumingcheng = row[x];
					disabledForm.value.shourumingcheng = true;
					continue;
				}
				if(x=='shourujine'){
					form.value.shourujine = row[x];
					disabledForm.value.shourujine = true;
					continue;
				}
				if(x=='shourubeizhu'){
					form.value.shourubeizhu = row[x];
					disabledForm.value.shourubeizhu = true;
					continue;
				}
				if(x=='zhichumingcheng'){
					form.value.zhichumingcheng = row[x];
					disabledForm.value.zhichumingcheng = true;
					continue;
				}
				if(x=='zhichujine'){
					form.value.zhichujine = row[x];
					disabledForm.value.zhichujine = true;
					continue;
				}
				if(x=='zhichushuoming'){
					form.value.zhichushuoming = row[x];
					disabledForm.value.zhichushuoming = true;
					continue;
				}
				if(x=='shouzhijieyu'){
					form.value.shouzhijieyu = row[x];
					disabledForm.value.shouzhijieyu = true;
					continue;
				}
				if(x=='dengjiriqi'){
					form.value.dengjiriqi = row[x];
					disabledForm.value.dengjiriqi = true;
					continue;
				}
				if(x=='dengjineirong'){
					form.value.dengjineirong = row[x];
					disabledForm.value.dengjineirong = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(context?.$toolUtil.storageGet("role")!="管理员" && !context?.$toolUtil.storageGet("isAdmin")) {
				disabledForm.value.shouzhijieyu = true;
			}
			if(context?.$toolUtil.storageGet("role")!="管理员" && !context?.$toolUtil.storageGet("isAdmin")) {
				disabledForm.value.dengjiriqi = true;
			}
		})
	}
	//初始化
	//声明父级调用
	defineExpose({
		init
	})
	//关闭
	const closeClick = () => {
		formVisible.value = false
	}
	//富文本
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//提交
	const save= async ()=>{
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = user.value.id
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(async res=>{
								emit('formModelChange')
								context?.$toolUtil.message(`操作成功`,'success')
                                formVisible.value = false
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(async (res)=>{
						emit('formModelChange')
						context?.$toolUtil.message(`操作成功`,'success')
                        formVisible.value = false
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData = async (row)=>{
		await context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
</script>
<style lang="scss" scoped>
	// 表单
	.formModel_form{
		// form item
		:deep(.el-form-item) {
			//label
			.el-form-item__label {
			}
			// 内容盒子
			.el-form-item__content {
				// 输入框
				.list_inp {
				}
				//日期选择器
				.list_date {
				}
				// 长文本
				.el-textarea__inner {
				}
			}
		}
	}
	// 按钮盒子
	.formModel_btn_box {
		.cancel_btn {
		}
		.cancel_btn:hover {
		}
		
		.confirm_btn {
		}
		.confirm_btn:hover {
		}
	}
</style>
